.gpt3__possibility {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  margin: 0 3rem;
  flex-direction: row;
}

.gpt3__possibility-image {
  flex: 1;
  margin-right: 1rem;
}

.gpt3__possibility-image img {
  width: 100%;
}

.gpt3__possibility-content {
  flex: 1;
}

.gpt3__possibility-content h4:first-of-type {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: var(--color-text);
}

.gpt3__possibility-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;

  margin: 1rem 0;
}

.gpt3__possibility-content p {
  color: var(--color-text);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
}

.gpt3__possibility-content h4:last-of-type {
  color: var(--color-subtext);
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;

  margin: 1rem 0;
  cursor: pointer;
}

@media screen and (max-width: 950px) {
  .gpt3__possibility {
    flex-direction: column;
  }
  .gpt3__possibility-image {
    margin: 2rem 0;
  }
}
