.gpt3__features {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.gpt3__features-heading {
  flex: 1;
  margin-right: 6rem;
}

.gpt3__features-heading h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
}

.gpt3__features-heading p {
  color: var(--color-subtext);
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;

  margin-top: 2rem;
  margin-bottom: 2rem;
}

.gpt3__features-container {
  flex: 1.5;
}

/* on Feature as need */
.gpt3__features-container .gpt3__features-container_feature {
  margin-bottom: 2rem;
}

@media screen and (max-width: 990px) {
  .gpt3__features {
    flex-direction: column;
  }

  .gpt3__features-heading {
    margin: 0 0 2rem 0;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__features-heading h1 {
    font-size: 28px;
    line-height: 38px;
  }
}
